.image {
    display: flex;
    align-items: center;
    width: 3vw;
    height: 5vh;
  }
  
    @media (max-width: 48em) {.image {
      height: 42px;
      width: 100%
  }
    }
  
    @media (hover: hover) {
  
    .image:hover {
      background-color: var(--mantine-color-gray-0);
    }
      [data-mantine-color-scheme='dark'] .image:hover {
      background-color: var(--mantine-color-dark-6);
    }
}
  
    @media (hover: none) {
  
    .image:active {
      background-color: var(--mantine-color-gray-0);
    }
      [data-mantine-color-scheme='dark'] .image:active {
      background-color: var(--mantine-color-dark-6);
    }
}